import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			items: ['主页', '图书', '杂志', '课程'],
			current: 0
		}
	},
	onLoad() {
			uni.showModal({
		    title: '提示',
			confirmText:'免费申请',
			cancelText:'我先看看',
		    content: '为了更好的享受企业服务,请先申请为企业会员',
		    success: function (res) {
		        if (res.confirm) {
		            console.log('用户点击确定');
		        } else if (res.cancel) {
		            console.log('用户点击取消');
		        }
		    }
		});
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		toEnterprise() {
			uni.reLaunch({
				url: '../index/enterprise/enterprise'
			});
		}
	}
}
